





















import { mapState } from 'vuex';
export default {
	data() {
		return {
			itemList: ['../../static/images/banner1.jpg','../../static/images/banner2.jpg'],
			indicatorDots: false,
			autoplay: false,
			interval: 5000,
			duration: 1000,
			listData: [
				{path:'inquiry',name:'车辆管理'},
				{path:'vehicle',name:'询价管理'},
				{path:'insurance',name:'保险管理'},
				{path:'repayment',name:'还款查询'}
			]
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	onLoad() {
	    if (!this.hasLogin) {
	        uni.showModal({
	            title: '未登录',
	            content: '您未登录，需要登录后才能继续',
	            /**
	             * 如果需要强制登录，不显示取消按钮
	             */
	            showCancel: !this.forcedLogin,
	            success: (res) => {
	                if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
	                    if (this.forcedLogin) {
	                        uni.reLaunch({
	                            url: '../login/login'
	                        });
	                    } else {
	                        uni.navigateTo({
	                            url: '../login/login'
	                        });
	                    }
	                }
	            }
	        });
	    };
		uni.startPullDownRefresh();
	},
	onPullDownRefresh() {
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onShareAppMessage() {
		return {
			title: '上海存德车贷首页',
			path: '/pages/home/home'
		}
	},
	mounted() {
		// this.getIronList()
	},
	methods: {
		/**
		 * 跳转对应页面
		 */
		go(path) {
			uni.navigateTo({
				url: `/pages/${path}/${path}`
			});
		},
	}
}
